.tile {
  position : relative;
  height: 150px;
  width: 100%;
  display: flex;
  flex-direction : row;
  justify-content : center;
  align-items : center;
  border-top : 0.5px solid lightgrey;
  padding: 0.5%;
  cursor : pointer;
}

.button {
  position: relative;
  margin: 5px;
  top : 8.5px;
}

.imgContStyle {
  background-color: rgba(41, 240, 26, 0);
}



@media only screen and (min-width : 800px) {

  .tile {
    box-shadow : 3px 3px 5px lightgrey;
    position : relative;
    height: 250px;
    width: 200px;
    display: flex;
    flex-direction : column;
    justify-content : center;
    align-items : center;
    border-radius : 10px;
    border : 0px;
    padding: 0px;
    margin: 20px;
    margin-top : 30px;
    margin-bottom : 30px;
    cursor : pointer;
  }

  .imgContStyle {
    background-color: rgba(41, 240, 26, 0.2);
  }

  .button {
    top : 0px;
  }
}
