img {
  object-fit: contain;
}

.snackbar, .main, .mobileToggler {
  display : flex;
  flex-direction: column;
}

.supplier {
  display : flex;
  flex-direction: row;
}

.main {
  height: 100%;
  width: 100%;
}

.topArea {
  width: 100%;
  height : auto;
  background : #4cb944;
}

.mobileToggler {
  justify-content: center;
  align-items: center;
  width : 40px;
  height : 40px;
  //border: 1px solid black;
  border-radius: 5px;
  //background-color: white;
  margin : 5px;
}

.mobileToggler img {
  object-fit: contain;
  width: 80%;
  height : 80%;
}

.navbar {
  margin-top: 0px;
}

@media only screen and (min-width : 800px) {

  .mobileToggler {
    display : none;
  }

  .sideNavToggler, .sideNavArrow {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }

  .sideNavToggler {
    position: absolute;
    z-index: 4;
    width: 50px;
    height: 100%;
    right: 98%;
  }

  .sideNavToggler:hover {
    background: rgba(0,0,0,0.7);
  }

  .sideNavArrow {
    width: 50%;
    height: 5%;
    border-radius: 0px 50px 50px 0px;
    background: white;
  }

  .arrow {
    height: 50%;
    width: 50%;
  }

  .orderDetails {
    width : auto;
    height: 90%;
    background: white;
    overflow-y: scroll;
  }

  .order {
    color : black;
    font-weight: normal;
    margin : 30px;
  }

  .detailsLogo {
    width : 30px;
    height: 30px;
    margin: 5px;
  }

  .detailsLogo img {
    width: 100%;
    height: 100%;
  }

  .supplier {
    margin-bottom: 20px;
    border-bottom: 2px solid #4cb944;
    align-items: center;
    width: 100%;
  }

  .products {
    width: 500px;
  }

  .row, .orderActions {
    display: flex;
    flex-direction: row;
  }

  .row {
    padding : 5px 0px 5px 0px;
    justify-content: space-between;
  }

  .subtotal {
    width: 25%;
    border-top: 1px dotted grey;
    padding: 10px 0px 10px 0px;
    float: right;
  }

  .orderActions {
    padding: 10px 0px 10px 0px;
  }

  .orderActions button {
    margin: 0px 10px 0px 10px;
  }



}
