.catalog {
  margin-top:  70px;
  width : 90%;
  display : flex;
  flex-direction : column;
  justify-content : center;
  align-items : center;
}

table {
  width : 100%;
  border-collapse: collapse;
  margin-bottom: 50px;
  border : 10px;
}

tr, table {
  //font-weight: normal;
  font-size: 95%;
  border: 1px solid lightgrey;
}

td, th {
  width: 60px;
  text-align: left;
  text-transform: 'uppercase';
}

th  {
  background: rgba(41, 240, 26, 0.5);
}

td {
  padding: 5px 20px 5px 20px;
  vertical-align: middle;
}

tr:nth-of-type(odd) {
  //background: rgba(41, 240, 26, 0.1);
}

th {
/*  background: rgb(235,235,235);
//  background: linear-gradient(180deg, rgba(235,235,235,1) 26%,
  rgba(209,207,207,1) 100%);*/
  color : black;
  padding : 20px;
  font-size: 80%;
  cursor : pointer;
}

td input {
  padding: 5px;
  margin: 5px;
  width : 50px;
  height : 20px
}

.tableImage {
  width : 30px;
  height : 30px;
  margin-right : 10px;
}

th:hover {
  background: rgba(41, 240, 26, 0.4);
}

@media only screen and (min-width : 800px) {
  table {
    box-shadow: 5px 5px 5px lightgray;
  }
   .catalog {
     display : flex;
     flex-direction : row;
     flex-wrap : wrap;
  }
}
