.snackbarCont {
  display: flex;
  flex-direction: column;
}

.snackbarCont {
  width : 100%;
  justify-content: center;
  align-items: center;
}

.snackbar {
  text-transform: capitalize;
  background-color: #2b303a;
  color : white;
  height : 80px;
  border-radius: 5px;
  justify-content: center;
  padding: 0px 10px 0px 10px;
  align-items: center;
  font-size: 120%;
  position: absolute;
  bottom : 50px;
  z-index: 2;
  box-shadow : 5px 5px 10px lightgrey;
}

.snackbar button {
  margin-left : 15px;
}

.view {
  width : 100%;
  height : 100%;
  overflow-y: scroll;
}

@media only screen and (min-width : 800px) {

  .snackbar {
    width: auto;
  }

}
