.loginContainer, .loginForm, form {
  display : flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.loginContainer {
  height: 100vh;
  background: rgba(250, 250, 250, 1);
}

form {
  width : 100%;
}

input {
  margin: 15px;
}

span {
  font-size: 80%;
}

.recovery {
  font-size: 80%;
  color: #246eb9;
  cursor: pointer;
}

.recovery:hover {
  text-decoration: underline;
}

