.navOption, .navOptionCont{
  display: flex;
  flex-direction: row;
}

.navbar {
  margin-top: 50px;
  box-shadow: none;
  width : 50%;
  box-shadow: none;
}

.navOption {
  justify-content: flex-start;
  width : 50%;
  color : white;
  cursor : pointer;
}

.navOptionCont:hover {
  background: rgba(225, 225, 225, 0.3);
}

.navOptionCont {
  justify-content: center;
  padding: 10px 0px 10px 0px;
  width : 100%;
}


@media only screen and (min-width : 800px)  {
  .navbar {
    width : 20%;
    height : inherit;
    margin: 0px;
    box-shadow: 5px 0px 5px gray;
  }
}
