.filters {
  display: flex;
  flex-direction: column;
}

.dropdown  {
  display: flex;
  flex-direction: row;
}

img {
  object-fit: contain;
  height : 100%;
  cursor: pointer;
}

.filters {

}

.dropdown {
  margin-top : 30px;
  width : 100%;
  justify-content: space-between;
}

select {
  margin-left:  10px;
  font-size: 100%;
}

.views {
  height : 30px;
  width : 100px;
  display : flex;
  flex-direction: row;
  justify-content: space-between;
}

.view {
  height : 30px;
  width : 30px;
  margin-left : 10px;
  padding: 6px;
  border-radius: 100px;
}

.view:hover {
  background: lightgrey;
}

.search {
  width : 100%;
  height : 150px;
  display : flex;
  flex-direction : column;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 10px;
}


@media only screen and (min-width : 800px) {
  .search {
    flex-direction: row;
    width : 80%;
    justify-content: center;
  }

}
